/*
 *  Featured Content 2
 *    Styles for the featured content 1 section.
 *
 *  Version:
 *    1.0.0 - 2021/10/23
 */

.c-featured-content-2 {
  padding: 5rem $s-gutter--edge-mobile;
  background-color: $s-color--background-subdued;

  &__inner {
    max-width: 31.25rem;
    margin: 0 auto;
  }

  // Columns
  &__column {
    &.is-image-column {
      margin-bottom: 2.875rem;
    }
  }


  // Images
  &__images {
    position: relative;
    padding-bottom: 9.55%;
  }

  // Image 1
  &__image-1 {
    &-wrapper {
      position: relative;
      width: 82.09%;
      margin-right: auto;
    }

    &-container {
      position: relative;
      padding-bottom: 92%;      
      display: block;

      &.no-pointer{
        cursor: default;
      }
    }
  }

  // Image 2
  &__image-2 {
    &-wrapper {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 38.21%;
    }

    &-container{
      display: block;

      &.no-pointer{
        cursor: default;
      }
    }
  }

  // Title
  &__title {
    max-width: 67.17%;
    margin-bottom: 1.5rem;
  }

  // Content
  &__content {
    margin-bottom: 1rem;

    &-container {
      padding: 0 0.8125rem 0 2.625rem;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    padding: 5rem 2.8125rem 5.3125rem;

    &__inner {
      max-width: none;
    }

    &__columns {
      display: flex;
      justify-content: space-between;
    }

    &__column {
      &.is-image-column {
        width: 55%;
        padding-right: 1rem;
      }

      &.is-content-column {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 45%;
        padding-left: 1rem;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 7.875rem #{'min(6%, 7.5rem)'} 7.5rem;

    &__inner {
      max-width: 110rem;
      margin: 0 auto;
    }

    &__column {
      &.is-image-column {
        width: 57.38636363636364%;
        padding-right: 0;
      }

      &.is-content-column {
        width: 34.09090909090909%;
        padding-left: 0;
        margin-right: 3.5rem;
      }
    }

    &__image-1-wrapper {
      width: 85.09%;
    }

    &__image-2-wrapper {
      width: 39.71%;
    }

    &__title {
      max-width: 100%;
      margin-bottom: 2.625rem;
    }

    &__content {
      margin-bottom: 2.5rem;

      &-container {
        padding-right: 0;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__column {
      &.is-content-column {
        transform: translateY(-7.4%);
        margin-right: #{'min(18.83%, 7.0625rem)'};
      }
    }

    &__title {
      margin-bottom: 4.5rem;
    }

    &__content {
      margin-bottom: 4.125rem;

      &-container {
        padding-left: 9.25rem;
      }
    }
  }
}
